import Vue from 'vue'
import Router from 'vue-router'
//1.引入插件  vue 和 vue-Router


import index from './routes/index.vue'
import play from './routes/play.vue'

//2.引入路由模板 

//3.通知全局开始配置路由
Vue.use(Router)

//4.配置路由

var routes = [
	{
		path:'/',
		name:'index',
		component:index,
	},
	{
		path:'/play',
		name:'play',
		component:play,
	},
]


//5.暴露一个对象
export default new Router({
	routes
})


